<template>
  <div class="more">
    <h2>司機詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">編輯</div>
	  </div>
	  <div class="more-c">
        <div class="more-d" v-for="(item,index) in list_key">
		  <span>{{item}}: {{list[item]}}</span>
		  <div class="more-d-edit">
		    <el-input :placeholder="item" v-model="list[item]" v-if="edit"></el-input>
		  </div>
		  <hr>
		</div>
		<div class="more-c-d">
          <div class="more-c-d-h">打卡紀錄</div>
		  <div class="more-c-d-c">
		  </div>
		</div>
		<div class="more-c-d">
          <div class="more-c-d-h">身心檢查紀錄</div>
		  <div class="more-c-d-c"></div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  list_key:[],
	  edit:false
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_driver_more(x)
	  this.list_key=Object.keys(this.list)
	  console.log(this.list)
	}
  }
}
</script>



