<template>
  <div class="more">
    <h2>個案詳細資料</h2>
	<div class="more-b">
	  <div class="more-h">
	    <div class="more-h-d" :class="{'more-h-d-on':!edit}" @click="edit=false">瀏覽</div>
		<div class="more-h-d" :class="{'more-h-d-on':edit}" @click="edit=true">編輯</div>
	  </div>
	  <div class="more-c">
		<el-button type="primary" icon="el-icon-document-checked">存檔</el-button>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">個案編號</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.CaseUserNo}}</div><div class="more-c-label" v-else><el-input v-model="list.CaseUserNo"></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">個案姓名</div></el-col>
	      <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.Name}}</div><div class="more-c-label" v-else><el-input></el-input v-model="list.Name"></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">性別</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.Sex==1 ? "男":"女"}}</div><div class="more-c-label" v-else><el-input></el-input v-model="list.CaseUserNo"></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">生日</div></el-col>
	      <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{this.Function.moment.yyyymmdd(list.Birthday)}}</div><div class="more-c-label" v-else><el-input></el-input v-model="list.CaseUserNo"></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">身分證字號</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UID}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">電子郵件</div></el-col>
	      <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.Email}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">手機</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.Phone}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">電話</div></el-col>
	      <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.Tel}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">社會福利身分</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit&&list_1.data">{{Function.get_index(list_1.data,list.UserType,"Id","TypeName")}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">失能等級</div></el-col>
	      <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.DisabilityId}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">緯度</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.lat}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">經度</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.lon}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">戶籍地址</div></el-col>
		  <el-col :sm="12" :md="5"><div class="more-c-label" v-if="!edit">{{list.CensusCounty}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="5"><div class="more-c-label" v-if="!edit">{{list.CensusDistrict}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="11"><div class="more-c-label" v-if="!edit">{{list.CensusAddr}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">通訊地址</div></el-col>
		  <el-col :sm="12" :md="5"><div class="more-c-label" v-if="!edit">{{list.CommCounty}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="5"><div class="more-c-label" v-if="!edit">{{list.CommDistrict}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="11"><div class="more-c-label" v-if="!edit">{{list.CommAddr}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">特約機構</div></el-col>
		  <el-col :sm="12" :md="21"><div class="more-c-label">
		    <!--<el-select v-model="comp" multiple placeholder="核定服務">
              <el-option v-for="(item,index) in list.CaseUserCompanyMapper" :key="index" :label="item.CarCompanys.CompanyName" :value="item.CarCompanys.Id"></el-option>
            </el-select>-->	  
		  </div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">緊急聯絡人姓名</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UrgentName}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">緊急聯絡人關係</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UrgentRelationship}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">緊急聯絡人手機</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UrgentPhone}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		  <el-col :sm="12" :md="3"><div class="more-c-label">緊急聯絡人電話</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UrgentTel}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<el-row :gutter="20">
		  <el-col :sm="12" :md="3"><div class="more-c-label">緊急聯絡人信箱</div></el-col>
		  <el-col :sm="12" :md="9"><div class="more-c-label" v-if="!edit">{{list.UrgentEmail}}</div><div class="more-c-label" v-else><el-input></el-input></div></el-col>
		</el-row>
		<div class="more-c-d">
          <div class="more-c-d-h">違規紀錄</div>
		  <div class="more-c-d-c" v-if="list.OrderViolationPoint&&list.OrderViolationPoint.OrderViolation">
		    <div class="more-c-d-c-d" v-for="(item,index) in list.OrderViolationPoint.OrderViolation">{{item.OrderDetails.SOrderNo}}</div>
		  </div>
		</div>
		<div class="more-c-d">
          <div class="more-c-d-h" @click="get_detail()">搭乘紀錄</div>
		  <div class="more-c-d-c">
		    <div class="more-c-d-c-d" v-for="(item,index) in Detail">{{item.SOrderNo}}</div>
		  </div>
		</div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  list_key:[],
	  edit:false,
	  list_30:[],  //身分別
	  list_30_text:[],
	  list_1:[],  //經濟狀況
	  list_1_text:[],
	  list_8:[],  //失能等級
	  list_8_text:[],
	  list_4:[],  //核定服務
	  list_4_text:[],
	  comp:[],
	  Detail:[],
	  Detail_num:0
	}
  },
  mounted:async function() {
	this.GLOBAL.show_black=true
	await this.all_ready()
	await this.get_data(this.$route.params.id)
	this.GLOBAL.show_black=false
	console.log(this.list)
	console.log(this.list_30_text)
	console.log(this.list_1)
	console.log(this.list_8_text)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.id)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.comp=await this.Function.get_CarCompanys()
	  this.list=await this.Function.get_case_more(x)
	  this.list_30=await this.Function.get_parentID(30)
	  this.list_30_text=this.list_30.data.map(x=>x.TypeName)
	  this.list_1=await this.Function.get_parentID(1)
	  this.list_1_text=this.list_1.data.map(x=>x.TypeName)
	  this.list_8=await this.Function.get_parentID(8)
	  this.list_8_text=this.list_8.data.map(x=>x.TypeName)
	  this.list_4=await this.Function.get_parentID(4)
	  this.list_4_text=this.list_8.data.map(x=>x.TypeName)
	  this.list_key=Object.keys(this.list)
	  this.list.CaseUserCompanyMapper=this.list.CaseUserCompanyMapper.map(x=>x.CarCompanys.Id)
	  console.log("list1")
	  console.log(this.comp)
	  console.log(this.list.CaseUserCompanyMapper)
	},
    get_detail:async function() {
	  this.GLOBAL.show_black=true
	  this.Detail_num+=5
	  this.Detail=await this.Function.get_case_OrderDetails(this.$route.params.id,this.Detail_num)
	  console.log(this.Detail) 
	  this.$forceUpdate()
	  this.GLOBAL.show_black=false
	}
  }
}
</script>



