<template>
  <div class="order">
    <h2>訂單詳細資訊</h2>
	  <div class="map" ref="map1"></div>
    <h2 v-if="list.OrderDetails">{{list.OrderDetails.SOrderNo}}</h2>
    <div class="order-time">
    <el-timeline>
      <el-timeline-item v-for="(item, index) in list.OrderStatusLog" :key="index" :timestamp="item.StatusChinese" :size="'large'">
      {{Function.moment.yyyymmddhhmmss(item.CreateDate)}}
      </el-timeline-item>
    </el-timeline>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
	  list:[],
	  maps:[]
	}
  },
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data()
	this.GLOBAL.show_black=false
	console.log(this.list)
  },
  watch:{
    async $route(to, from){
    }
  },
  methods:{
    get_data:async function(x) {
	  this.maps[0]=this.Function.initMap(this.$refs.map1)
      this.list=await this.Function.get_OrderDetails_more(this.$route.params.id)
	  console.log(this.list)
	  this.Function.calculateAndDisplayRoute(this.maps[0],this.list.OrderDetails.FromAddr,this.list.OrderDetails.ToAddr)
	}
  }
}
</script>



